*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  background-color: black;
}
.container{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
}
.calc{
  padding: 20px;
  border-radius: 20px;
  background-color: transparent;
  border: none;
  box-shadow: rgba(21, 148, 203, 0.25) 0px 54px 55px, rgba(21, 148, 203, 0.25) 0px -12px 30px, rgba(21, 148, 203, 0.25) 0px 4px 6px, rgba(21, 148, 203, 0.25) 0px 12px 13px, rgba(21, 148, 203, 0.25) 0px -3px 5px;
}
#input{
  width: 100%;
  max-width: 400px;
  border-radius: 20px;
  height: 80px;
  padding-top: 15px;
  border: none;
  background: transparent;
  text-align: center;
  color: white;
  color: #2bc9c6;
  text-shadow: 0 0 10px #2bc9c6, 0 0 20px #2bc9c6, 0 0 30px #2bc9c6;
  animation: neon-glow 1.5s ease-in-out infinite alternate;
}

@keyframes neon-glow {
  0% {
    text-shadow: 0 0 10px #2bc9c6, 0 0 20px #2bc9c6, 0 0 30px #2bc9c6;
  }
  100% {
    text-shadow: 0 0 20px #2bc9c6, 0 0 30px #2bc9c6, 0 0 40px #2bc9c6;
  }
}


button{
  border: none;
  color: white;
  margin: 10px;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: transparent;
  box-shadow: rgba(25, 181, 216, 0.4) 0px 2px 4px, rgba(25, 181, 216, 0.4) 0px 7px 13px -3px, rgba(25, 181, 216, 0.4) 0px -3px 0px inset;
    cursor: pointer;
}